import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../../../environments/environment';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root',
})
export class ReferralService {

  constructor(private http: HttpClient) {}

  getReferralData(): Observable<any> {
    const token = localStorage.getItem('authToken'); // Retrieve token
    const headers = new HttpHeaders().set('Authorization', `Bearer ${token}`);
    return this.http.get<any>(`${environment.BACKEND_URL}/admin/refferal`, { headers });
  }
  
  updateReferralData(profileData: any): Observable<any> {
    const token = localStorage.getItem('authToken'); // Retrieve token
    const headers = new HttpHeaders().set('Authorization', `Bearer ${token}`);
    return this.http.patch<any>(`${environment.BACKEND_URL}/admin/refferal/1`, profileData, { headers });
  }
  getReferralPointsData(): Observable<any> {
    const token = localStorage.getItem('authToken'); // Retrieve token
    const headers = new HttpHeaders().set('Authorization', `Bearer ${token}`);
    return this.http.get<any>(`${environment.BACKEND_URL}/admin/refferal-points`, { headers });
  }


  updateReferralPointsData(profileData: any): Observable<any> {
    const token = localStorage.getItem('authToken'); // Retrieve token
    const headers = new HttpHeaders().set('Authorization', `Bearer ${token}`);
    return this.http.patch<any>(`${environment.BACKEND_URL}/admin/refferal-points/1`, profileData, { headers });
  }
}
