import { Component,OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ProfileService } from './profile.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrl: './profile.component.scss'
})
export class ProfileComponent {
  profileForm: FormGroup;
  profileImage: string = ''; // Store Base64 Image
  passwordVisibility: boolean = false;

  constructor(private fb: FormBuilder, private profileService: ProfileService) {
    this.profileForm = this.fb.group({
      first_name: ['', Validators.required],
      last_name: ['', Validators.required],
      email: [{ value: '', disabled: true }, Validators.required],
      profile_img: [''], // Base64 image will be stored here
    });
  }

  ngOnInit(): void {
    this.loadUserProfile();
  }
  public onFileSelected(event: any): void {
    const file = event.target.files[0]; // Get the selected file
    if (file) {
      const reader = new FileReader();
      reader.readAsDataURL(file); // Read file as DataURL (Base64)
      reader.onload = () => {
        this.profileImage = reader.result as string; // Store Base64 string
        this.profileForm.patchValue({ profile_img: this.profileImage }); // Update form value
      };
      reader.onerror = (error) => {
        console.error('Error converting image to Base64:', error);
      };
    }
  }

  loadUserProfile(): void {
    this.profileService.getProfile().subscribe((data) => {

      let Obj = data.results
      this.profileForm.patchValue({
        first_name: Obj.first_name,
        email: Obj.email,
        last_name: Obj.last_name,
        profile_image: Obj.profile_image, // Base64 image from API
      });
      this.profileImage =Obj.profile_img_url
    });
  }

  togglePassword(): void {
    this.passwordVisibility = !this.passwordVisibility;
  }

  updateProfile(): void {
    if (this.profileForm.valid) {
      this.profileService.updateProfile(this.profileForm.value).subscribe(() => {
        Swal.fire({
                  icon: 'success',
                  title: 'Profile updated Successfully!',
                  confirmButtonColor: '#3085d6',
                  confirmButtonText: 'OK',
                });
      });
    } else {
      alert('Please fill in all required fields.');
    }
  }

  onImageUpload(event: any): void {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e: any) => {
        this.profileImage = e.target.result; // Convert file to Base64
        this.profileForm.patchValue({ profileImage: this.profileImage });
      };
      reader.readAsDataURL(file);
    }
  }
}