import { Component,OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ReferralService } from './refferal.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-profile',
  templateUrl: './refferal.component.html',
  styleUrl: './refferal.component.scss'
})
export class RefferalComponent {
  referralForm: FormGroup;
  referralPointsForm!: FormGroup;
  passwordVisibility: boolean = false;

  constructor(private fb: FormBuilder, private referralService: ReferralService) {
    this.referralForm = this.fb.group({
      android_link: ['', [Validators.required, Validators.pattern('https?://.+')]], // URL validation
      ios_link: ['', [Validators.required, Validators.pattern('https?://.+')]],
    });
    this.referralPointsForm = this.fb.group({
      points_for_referrer: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
      points_for_referee: ['', [Validators.required, Validators.pattern('^[0-9]+$')]],
    });
  }

  ngOnInit(): void {
    this.loadUserRefferalLinks();
    this.loadUserRefferalPoints();
  }

  loadUserRefferalLinks(): void {
    this.referralService.getReferralData().subscribe((data) => {
      let Obj = data.results
      console.log(Obj[Obj.length-1])
      this.referralForm.patchValue({
        android_link: Obj[Obj.length-1].android_link,
        ios_link: Obj[Obj.length-1].ios_link,
      });
    });
  }
  loadUserRefferalPoints(): void {
    this.referralService.getReferralPointsData().subscribe((data) => {
      let Obj = data.results
      console.log(Obj)
      this.referralPointsForm.patchValue({
        points_for_referrer: Obj[Obj.length-1].points_for_referrer,
        points_for_referee: Obj[Obj.length-1].points_for_referee,
      });
    });
  }

  togglePassword(): void {
    this.passwordVisibility = !this.passwordVisibility;
  }

  updateReferral() {
    if (this.referralForm.invalid) {
      return;
    }
    this.referralService.updateReferralData(this.referralForm.value).subscribe(
      (response) => {
        Swal.fire({
          icon: 'success',
          title: 'Updated Successfully!',
          text: 'Referral links have been updated.',
          confirmButtonColor: '#3085d6',
          confirmButtonText: 'OK',
        });
        console.log('Referral updated successfully', response);
      },
      (error) => {
        console.error('Error updating referral data', error);
      }
    );
  }
  updateReferralPoints() {
    if (this.referralPointsForm.invalid) {
      return;
    }
    this.referralService.updateReferralPointsData(this.referralPointsForm.value).subscribe(
      (response) => {
        Swal.fire({
          icon: 'success',
          title: 'Updated Successfully!',
          text: 'Referral Points have been updated.',
          confirmButtonColor: '#3085d6',
          confirmButtonText: 'OK',
        });
        console.log('Referral updated successfully', response);
      },
      (error) => {
        console.error('Error updating referral data', error);
      }
    );
  }

}